
.home {
    display: flex;
    background-image: url("/src/Assets/hero.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    align-items: center;
   height: 100vh;
}

.home-content h3{
    font-size: 3.2rem;
    font-weight: 700;
}

.home-content h3:nth-of-type(2) {
    margin-bottom: 2rem;
}

span {
    color: var(--main-color);
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: rgb(0, 0, 0);
    font-size: 4rem;
    text-shadow: rgb(0, 0, 0) 1px 0 10px;
}
.typed-text {
    color: var(--main-color);
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: rgb(0, 0, 0);
    font-size: 4rem;
    text-shadow: rgb(0, 0, 0) 1px 0 10px;
}

.home-content h1 {
    font-size: 5.6rem;
    font-weight: 700;
    line-height: 1.3;
}

.social-media a{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 4rem;
    height: 4rem;
    background: transparent;
    border: .2rem solid var(--main-color);
    border-radius: 50%;
    font-size: 2rem;
    color: var(--main-color);
    margin: 3rem 1.5rem 3rem 0;
    transition: .5s ease;
}

.social-media a:hover {
    background-color: var(--main-color);
    color: var(--second-bg-color);
    box-shadow: 0 0 1rem var(--main-color);
}

.btn {
    display: inline-block;
    padding: 1rem 2.8rem;
    background: var(--main-color);
    border-radius: 4rem;
    box-shadow: 0 0 1rem var(--main-color);
    font-size: 1.6rem;
    color: var(--second-bg-color);
    letter-spacing: .1rem;
    font-weight: 600;
}

.btn:hover {
    box-shadow: none;   
}






/* Media query 768px */
@media screen and (max-width: 820px) {
    .home {
        background-size: contain; /* Keeps the aspect ratio*/
        background-position: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        height: 100vh;
    }

    .home-content {
        text-align: center;
        max-width: 90%; 
    }

    .home-content h1 {
        font-size: 4.8rem;
        line-height: 1.2;
    }

    .typed-text {
        font-size: 3.2rem;
    }
}
/* Media query 375px */
@media screen and (max-width: 750px) {
    .home {
        background-size: contain; 
        background-position: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
    }

    .home-content {
        text-align: center;
        max-width: 90%; 
    }

    .home-content h1 {
        font-size: 3.6rem;
        line-height: 1.1;
    }

    .typed-text {
        font-size: 2.4rem;
    }

  
}
